.hero {
  font-family: $mainFontBold;
  font-weight: 700;
  line-height: 130%;

  &.container {
    // padding-left: 20px;
    // @media (max-width: 720px) {
    //   padding-left: 0;
    // }
    // @media (max-width: 569px) {
    //   padding-left: 16px;
    // }

    // @media (min-width: 570px) {
    //   padding-left: 20px;
    // }

  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 25px;
    margin-bottom: 35px;

    @media (min-width: $xs) {
      margin-top: 14px;
      margin-bottom: 35px;
    }
    @media (min-width: $md) {
      margin-top: 38px;
      margin-bottom: 54px;
    }
  }
  &__clubs-info {
    display: flex;
    margin-bottom: 25px;

    @media (max-width: $xs) {
      // padding: 0 16px;
    }

    @media (min-width: $xs) {
      margin-bottom: 35px;
      padding-right: 75px;
      padding-left: 4px;
    }

    @media (min-width: $md) {
      margin-bottom: 0;
      padding-right: 0;
    }
  }

  &__clubs-info,
  &__pic {
    width: 100%;
    // margin-bottom: 35px;

    @media (min-width: $md) {
      width: 50%;
      // margin: 2rem 0;
    }
  }

  &__pic {
    display: flex;
    justify-content: flex-end;

    @media (min-width: $md) {
      padding: 0 0 0 3rem;
    }

    & img {
      width: 324px;
      width: 100%;
      min-height: 217px;
      max-width: 100%;
      height: 100%;
      box-shadow: $boxShadow;
      border-radius: $borderRadius;

      @media (min-width: $sm) {
        width: 100%;
        min-height: 269px;
      }

      @media (min-width: $md) {
        width: 100%;
        // min-height: 305px;
        margin-left: auto;
        max-height: 430px;
        box-shadow: none;
      }

      @media (max-width: $md) {
        width: 100%;
      }
    }

    & picture {
      width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 130%;
    font-size: 20px;

    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        375px: 20px,
        768px: 50px,
        1440px: 60px,
      )
    );

    @media (min-width: $xs) {
      // padding: 0 16px;
      padding-left: 4px;
    }

    @media (min-width: $sm) {
      color: rgba(73, 76, 79, 0.5);
    }
    @media (min-width: $md) {
      margin-bottom: 1rem;
    }

    & span {
      display: block;

      @media (min-width: $md) {
        display: inline-block;
      }
    }
  }
}
