.map {
  position: relative;
  width: 100%;
  max-width: 100%;
  margin-top: 0;

  // background-image: url('../img/clubs/map.jpg');
  background-size: cover;
  border-bottom-left-radius: 22px;
  border-bottom-right-radius: 22px;

  height: 545px;

  @media (min-width: $sm) {
    height: 640px;
  }
  @media (min-width: $xl) {
    height: 820px;
  }

  // &.container {
  //   position: relative;
  //   height: 100%;

  //   @media (min-width: $md) {
  //     margin-bottom: 50px;
  //   }
  //   @media (min-width: $lg) {
  //     margin-bottom: 70px;
  //   }
  // }

  &--region {
    overflow: hidden;
    // margin-top: 25px;
    margin-bottom: 35px;
    width: 100%;
    min-height: 500px;
    height: 650px;

    @media (min-width: $xs) {
      width: 100%;
      margin-top: 25px;
    }
    @media (min-width: $sm) {
      // margin-top: 50px;
      margin-top: 35px;
    }
    @media (min-width: $md) {
      // height: 833px;
      margin-top: 44px;
      margin-bottom: 54px;
    }

    &.container {
      min-height: 100%;
    }

    & #map {
      background-color: $blue-color;
      border-radius: $borderRadius22;
    }
  }

  &__ya {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    -ms-touch-action: auto !important;
    touch-action: auto !important;
  }

  & [class$='-controls__control_toolbar'] {
    @media (max-width: $xs) {
      margin-right: 0 !important;
      padding: 5px;
    }
  }
  & [class$='-custom-scroll'] {
    box-shadow: -1px 4px 24px rgba(0, 0, 0, 0.2);
    border-radius: 22px;
    border: none;
    overflow: hidden;
  }

  & [class$='-balloon__content'] {
    padding: 0;
    margin: 0;

    & ymaps {
      min-height: 470px;
    }
  }
  & [class$='-balloon__close'] {
    position: absolute;
    top: 5px;
    right: 5px;

    & + [class$='-balloon__content'] {
      margin: 0;
    }
  }

  & .customControl {
    z-index: 10;
    display: none;
    position: relative;
    background-color: #f6f6f6;
    padding: 0;
    border-radius: 3px;
    max-width: 350px;
    box-shadow: -1px 4px 24px rgba(0, 0, 0, 0.2);
    border-radius: 22px;

    @media (min-width: $xs) {
      max-width: 460px;
    }
    @media (min-width: $md) {
      max-width: 645px;
    }
  }

  & .closeButton {
    position: absolute;
    right: 15px;
    top: 15px;
    z-index: 10000;
    width: 23px;
    height: 23px;
    flex-basis: 15px;
    flex-grow: 0;
    flex-shrink: 0;

    padding: 3px;

    cursor: pointer;
    // background: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNCIgaGVpZ2h0PSIxNCI+PHBhdGggZmlsbC1ydWxlPSJldmVub2RkIiBkPSJNMTQgLjdsLS43LS43TDcgNi4zLjcgMCAwIC43IDYuMyA3IDAgMTMuM2wuNy43TDcgNy43bDYuMyA2LjMuNy0uN0w3LjcgN3oiIGNsaXAtcnVsZT0iZXZlbm9kZCIvPjwvc3ZnPg==')
    //   50% no-repeat;
    background: url('../img/icons/close.svg') 50% no-repeat;
    // opacity: 0.3;
  }

  & .content {
    margin: 0;
  }

  // & .modal {
  //   position: absolute;
  // }

  &__sidebar {
    z-index: 9;
    position: absolute;
    top: 0;
    right: 0;
    margin: auto;
    width: 100%;
    max-width: 450px;
    height: 108%;
    padding: 0 0.25rem;

    transform: translate3d(0%, 0, 0);

    overflow: hidden;

    border-radius: $borderRadius22;

    @media (min-width: $xs) {
      top: -20px;
    }
    @media (min-width: $md) {
      width: 650px;
      max-width: 650px;
    }
  }
}

.ymaps-map {
  -ms-touch-action: auto !important;
  touch-action: auto !important;
}
