.slider {
  // margin-bottom: 1rem;
  min-height: 295px;


  border-radius: $borderRadius;
  box-shadow: $boxShadow;
  overflow: hidden;

  & .swiper {
    width: 100%;
  }

  & .swiper-container {
    position: relative;
    width: 100%;
  }

  & .swiper-slide {
    background-size: cover;
    background-position: 50%;
    min-height: 295px;


    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    text-decoration: none;

    & picture {
      width: 100%;
      height: 100%;
    }

    & img {
      height: 100%;
      object-fit: fill;
    }
  }

  // overwrite swiper defaults
  & .swiper-pagination {
    bottom: 18px;
    left: 50%;
    transform: translateX(-50%);

    &-bullet {
      margin: 0 6px;
      background-color: $white-color;
      border: 2px solid #fff;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      opacity: 1;
    }

    &-bullet-active {
      background-color: transparent;
    }
  }

  & .swiper-button {
    &-container {
      background-color: rgba(0, 0, 0, 0.25);
    }

    &-prev,
    &-next {
      z-index: 4;
      color: $white-color;

      &:focus,
      &:hover {
        color: $yellow-color;
      }
    }
  }

  &--content {
    height: 360px;
    @media (min-width: $xs) {
      height: 400px;
    }
    @media (min-width: $md) {
      height: 500px;
    }

    & img {
      min-height: 360px;

      @media (min-width: $xs) {
        min-height: 400px;
      }
      @media (min-width: $md) {
        min-height: 500px;
      }
    }

    & .swiper-container {
      height: 100%;
    }

    & .swiper-slide {
      background-position: center;
      background-size: cover;
      border-radius: $borderRadius;
      overflow: hidden;
    }
  }

  &--slide-br {
    & .swiper-slide {
      background-position: center;
      background-size: cover;
      border-radius: $borderRadius;
      overflow: hidden;
    }
  }

  &--sale {
    height: auto;
    max-height: 500px;

    & img {
      max-height: 500px;

    }

    & .swiper-slide {
      background-position: center;
      background-size: cover;
      border-radius: $borderRadius;
      overflow: hidden;
    }

    & .swiper-slide img {
      display: block;
      width: 100%;
    }

    & .swiper-button-next {
      right: 17px;
    }
    & .swiper-button-prev {
      left: 17px;
    }
    & .swiper-button-next:after,
    & .swiper-button-prev:after {
      font-size: 2.45rem;
    }

    & .swiper-pagination-fraction {
      bottom: 10px;
      left: 35px;
      color: $white-color;
      font-family: $mainFontBold;
      font-size: 20px;
    }

    & img {
      height: 100%;
      min-height: 330px;
    }
  }
}
