.lesson {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  width: 100%;
  height: 100%;
  border: 12px;
  background-color: #eae8ca;

  &::before {
    content: attr();
  }

  @media (min-width: $lg) {
    padding: 10px;
  }

  &--pool {
    background-color: #cde0f0;

    & .lesson__place {
      &::before {
        content: '';
        position: absolute;
        top: 4px;
        left: 0;
        width: 16px;
        height: 10px;
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url('../img/icons/sch-pool.svg');
      }
    }
  }

  &--pay {
    background-color: #f9d288;
  }

  &__wrapper {
    margin: 2px 0;
  }

  &__name {
    display: inline-block;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 15px;
    line-height: 130%;
    margin-bottom: 4px;

    &:focus,
    &:hover {
      text-decoration: underline;
    }

    @media (min-width: $md) {
      font-size: 13px;
    }
    @media (min-width: $xl) {
      font-size: 15px;
    }

    @media (min-width: $lg) {
      margin-bottom: 10px;
    }
  }

  &__trainer {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    margin-bottom: 2px;
    min-height: 18px;

    &::before {
      content: '';
      position: absolute;
      top: 3px;
      left: 2px;

      width: 13px;
      height: 13px;
      background-repeat: no-repeat;
      background-size: cover;
      background-image: url('../img/icons/sch-user.svg');
    }

    & span {
      width: calc(100% - 15px);
      padding-left: 5px;
    }

    @media (min-width: $md) {
      font-size: 13px;

      & span {
        padding-left: 3px;
      }
    }
    @media (min-width: $xl) {
      font-size: 15px;

      & span {
        width: calc(100% - 15px);
        padding-left: 5px;
      }
    }

    @media (min-width: 1100px) {
      line-height: 120%;
      margin-bottom: 7px;
    }
  }

  &__place {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    min-height: 18px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;

      width: 17px;
      height: 15px;
      background-repeat: no-repeat;
      background-size: cover;
      background-image: url('../img/icons/sch-spa.svg');
    }

    @media (min-width: $md) {
      font-size: 13px;
    }
    @media (min-width: $xl) {
      font-size: 15px;
    }

    @media (min-width: 1100px) {
      line-height: 120%;
    }

    & span {
      padding-left: 5px;
      width: calc(100% - 15px);

      @media (min-width: $md) {
        padding-left: 3px;
      }
      @media (min-width: $xl) {
        padding-left: 5px;
      }
    }
  }

  &__text {
    margin-bottom: 1rem;
  }

  &__popup {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 300px;
    height: 300px;
    background-color: #fff;
  }

  &__link {
    position: relative;
    display: inline-flex;
    padding-right: 40px;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 13px;
    line-height: 140%;

    &::before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 0;
      transform: translateY(-40%);
      width: 30px;
      height: 100%;
      background-image: url('../img/icons/arrow.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      transition: 0.3s;
    }

    &:hover {
      &::before {
        transform: translateY(-40%) translateX(10%);
      }
    }

    & span {
      &::first-letter {
        text-transform: uppercase;
      }
    }
  }
}

.popover {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 320px;
  background: #f6f7f8;
  box-shadow: 0px 4px 4px 1px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  height: auto;
  max-height: 300px;

  @media (min-width: $sm) {
    min-width: 380px;
    max-height: 233px;
  }

  & .popover-body {
    height: auto;
    max-height: 300px;
    @media (min-width: $sm) {
      min-width: 380px;
      max-height: 233px;
    }
  }

  & .lesson__popover-close {
    // @include linkReset();
    position: absolute;
    top: 10px;
    right: 12px;
    width: 20px;
    height: 20px;
    background-image: url('../img/icons/close.svg');
    background-size: contain;
    cursor: pointer;
  }

  & .title {
    padding-right: 15px;
    margin-bottom: 5px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 13px;
    line-height: 130%;
  }

  & .text {
    padding-right: 15px;
    margin-bottom: 5px;
    font-family: $mainFont;
    font-weight: 400;
    font-size: 13px;
    line-height: 130%;
    height: 100%;
    max-height: 170px;

    overflow-y: auto;
    // overflow: hidden;

    @media (min-width: $sm) {
      max-height: 130px;
    }

    @media (min-width: $md) {
      max-height: 110px;
    }

    @media (min-width: $xl) {
      max-height: 100px;
    }

    &::-webkit-scrollbar {
      width: 7px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.7);
      border-radius: 8px;
    }

    &::-webkit-scrollbar-thumb {
      background: #c4c4c4;
      border-radius: 41px;
    }
  }

  & .lesson__link {
    display: inline-flex;
    margin-top: auto;
    margin-bottom: 0;
    height: 30px;

    // @media (min-width: $sm) {
    //   margin-top: 20px;
    // }
    // @media (min-width: $md) {
    //   margin-top: 10px;
    // }
  }
}
