.clubs-search {
  &.container {
    position: relative;
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;

    padding-left: 16px;
    padding-right: 16px;

    @media (min-width: $md) {
      padding: 0;
    }
  }

  &__nav-btns {
    z-index: 3;
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: -10px;

    @media (min-width: $xs) {
      display: none;
    }
  }

  &__body {
    position: relative;
    overflow: hidden;
    padding-bottom: 10px;
    padding-top: 10px;
    margin-top: -10px;
  }

  &__btn-list {
    z-index: 5;
    position: absolute;
    top: 20px;
    right: 10px;
    background-color: $white-color;
    color: $primary-color;

    @media (min-width: 700px) {
      display: none;
    }

    &:hover {
      background-color: $yellow-color;
      color: $primary-color;
    }
  }

  &__sidebar {
    z-index: 0;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    overflow: hidden;
    border-radius: $borderRadius22;

    & .btn-close {
      z-index: 3;
      position: absolute;
      top: 20px;
      right: 20px;
      opacity: 1;

      @media (min-width: $xl) {
        // right: 30px;
      }
    }

    &.show {
      z-index: 2;
      width: 330px;

      @media (min-width: $xs) {
        width: 445px;
      }
      @media (min-width: $xl) {
        width: 670px;
      }
    }
  }
}

.sidebar-clubs__body {
  height: 100%;
  overflow-y: auto;
}

#scroll-cards {
  height: 100%;
}

.sidebar-clubs__list-clubs {
  position: relative;
  top: 0;
  left: 0;
  // width: 100%;

  display: flex;
  flex-direction: column;
}
