.full-schedule {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 30px;

  overflow: hidden;
  scroll-behavior: smooth;

  @media (max-width: 990px) {
    overflow-x: auto;
  }

  &__inner {
    @media (max-width: 990px) {
      min-width: 940px;
      width: 100%;
    }
  }

  &__header {
    display: flex;
    gap: 10px;
  }

  &__title {
    flex-grow: 1;
    padding: 20px;
    width: calc(100% / 6);

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;

    &:first-child {
      width: 160px;
      min-width: 160px;
    }

    &:nth-child(2) {
      min-width: 208px;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    background: #eaeced;
    border-radius: 12px;

    &:nth-child(2n) {
      background: #dee8ed;
    }
  }
}
