.card-club {
  display: flex;
  flex-direction: column;

  width: 100%;
  height: 100%;
  min-width: 288px;
  max-width: 620px;
  padding-top: 25px;
  padding-bottom: 25px;

  border-radius: $borderRadius22;
  box-shadow: $boxShadowdark;

  @media (min-width: 600px) {
    min-width: auto;
    // width: 50%;

    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: 670px) {
    min-width: 300px;
  }

  @media (min-width: $md) {
    padding-top: 43px;
    padding-bottom: 43px;
  }

  &__header {
    display: flex;
    flex-direction: column;

    @media (min-width: $xs) {
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
    }

    @media (min-width: $md) {
      display: grid;
      // grid-template-columns: minmax(346px, 1fr) minmax(170px, 1fr);
    }
  }

  &__header,
  &__footer {
    width: 100%;
    padding-left: 18px;
    padding-right: 18px;
    @media (min-width: $sm) {
      padding-right: 25px;
      padding-left: 25px;
    }
    @media (min-width: $md) {
      padding-right: 35px;
      padding-left: 35px;
    }
  }

  &__contacts {
    @media (min-width: $md) {
      width: 60%;
      flex-grow: 1;
    }
  }

  &__title {
    font-family: $mainFontBold;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    text-transform: uppercase;

    & span {
      display: inline-block;
      white-space: nowrap;

      @media (min-width: $xs) {
        display: block;
      }
      @media (min-width: $xl) {
        display: inline-block;
      }
    }
  }

  &__title-and-raiting {
    @media (min-width: $md) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 1;
      grid-row-end: 2;
    }
  }

  &__working {
    @media (min-width: $md) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 2;
      margin-left: auto;
    }
  }

  &__address {
    width: 100%;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    margin-bottom: 8px;

    @media (min-width: $sm) {
      font-size: 18px;
    }
    @media (min-width: $md) {
      font-size: 20px;

      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 3;
    }
  }

  &__working.working-hours {
    display: none;
    @media (min-width: $sm) {
      display: flex;
    }
    @media (min-width: $md) {
      width: auto;
      min-width: auto;
    }
  }

  &__pic {
    width: 100%;
    display: block;
    height: 175px;
    margin-top: 15px;
    margin-bottom: 25px;
    background-color: $blue-color;
    text-decoration: none;

    &:focus,
    &:hover {
      text-decoration: none;
    }

    @media (min-width: $sm) {
      height: 170px;
    }
    @media (min-width: $md) {
      height: 228px;
    }

    & img {
      width: 100%;
      height: 100%;
    }
  }

  &__footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    // align-items: center;
    flex-direction: column;

    @media (min-width: 769px) {
      flex-direction: row;
      align-items: center;
    }

    @media (min-width: $md) {
      flex-direction: row-reverse;
    }

    & .link-icon {
      &:first-child {
        margin-bottom: 0.5rem;
      }

      @media (min-width: 769px) {
        &:first-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &--three {
    padding: 25px 0;
    @media (min-width: $md) {
      width: 400px;
    }

    & .card-club__header,
    & .card-club__footer {
      @media (min-width: $md) {
        padding: 0 25px;
      }
    }
    & .card-club__footer {
      @media (min-width: 769px) {
        flex-direction: column;
        align-items: flex-start;
        gap: 14px;
      }
    }

    & .card-club__title {
      @media (min-width: $md) {
        font-size: 18px;
      }
    }

    & .card-club__address {
      @media (min-width: $sm) {
        // min-height: 47px;
      }
      @media (min-width: $md) {
        margin-bottom: 0;
        font-size: 18px;
        min-height: auto;
      }
    }

    & .card-club__pic {
      @media (min-width: $md) {
        height: 174px;
      }
    }

    & .working-hours__club {
      padding-left: 24px;
    }

    & .working-hours__club-label span {
      padding-left: 0.25rem;
    }
  }

  &--four {
    padding: 25px 0;

    & .card-club__header,
    & .card-club__footer {
      @media (min-width: $md) {
        padding: 0 25px;
      }
    }

    & .card-club__header {
      @media (min-width: $md) {
        display: grid;
        grid-template-columns: 1fr;
      }
    }

    & .card-club__title {
      & span {
        display: inline-block;
        @media (min-width: $xs) {
          display: block;
        }
      }
    }

    & .card-club__title-and-raiting {
      @media (min-width: $md) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 2;
      }
    }
    & .card-club__working {
      margin-right: auto;
      margin-left: 0;
      @media (min-width: $md) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 3;
        grid-row-end: 4;
      }
    }

    & .card-club__address {
      @media (min-width: $sm) {
        // min-height: 47px;
      }
      @media (min-width: $md) {
        font-size: 18px;
        min-height: auto;
      }
    }

    & .card-club__footer {
      @media (min-width: $xs) {
        gap: 14px;
        flex-direction: row;
      }
    }
  }

  &--map {
    box-shadow: none;
    border-radius: 0;
    padding: 21px 0;

    @media (min-width: $xs) {
      padding: 32px 0;
    }
    @media (min-width: $sm) {
      padding-top: 43px;
      padding-bottom: 32px;
    }

    & .card-club__text {
      display: none;
      font-family: inherit;
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 140%;

      @media (min-width: $xs) {
        display: flex;
        padding: 0 35px;
        // margin-top: 25px;
        margin-bottom: 25px;
      }

      @media (min-width: $sm) {
        padding-right: 40px;
      }
    }

    & .card-club__address {
      margin-bottom: 15px;

      @media (min-width: $xs) {
        margin-bottom: 8px;
      }
    }

    & .card-club__header {
      width: 100%;
      padding: 0 35px;

      @media (max-width: $xs) {
        padding: 0 20px;
        padding-right: 50px;
      }
    }

    & .card-club__title span {
      display: inline-block;
    }

    & .card-club__pic {
      order: 2;
      margin-bottom: 0;
      display: block;
      width: 100%;

      @media (min-width: $xs) {
        order: 0;
        margin-bottom: 25px;
      }
    }

    & .link-icon {
      text-decoration: none;
    }

    & .card-club__footer {
      flex-direction: column-reverse;
      align-items: flex-start;
      gap: 10px;
      padding: 0 30px 0 35px;

      @media (max-width: $xs) {
        padding: 0 20px;
      }

      @media (max-width: $xs) {
        & .link-icon {
          font-style: normal;
          font-weight: 400;
          font-size: 15px;
          line-height: 140%;
          margin-bottom: 0;
          padding: 10px 24px;
          border-radius: $borderRadius;

          &:first-child {
            padding-right: 24px;
            color: $primary-color;
            border: 1px solid $primary-color;
          }
          &:last-child {
            color: $white-color;
            background-color: $red-color;
          }
        }
      }

      @media (max-width: 365px) {
        & .link-icon {
          padding-right: 14px;
        }
      }

      @media (min-width: $xs) {
        width: 100%;
        flex-wrap: wrap;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;

        & .link-icon {
          margin-bottom: 0;
        }
      }
    }

    & .card-club__working.working-hours {
      display: flex;
      margin-bottom: 25px;

      @media (min-width: $xs) {
        display: none;
      }
      @media (min-width: $md) {
        display: flex;
      }
    }
  }

  &--list-item {
    padding-bottom: 0;
    background-color: $card-bg;
    overflow: hidden;
    max-width: 670px;
    border-radius: 0;

    @media (max-width: 699px) {
      background-color: $yellow-color;

      &:nth-child(2n) {
        background-color: $blue-color;
      }
    }

    @media (min-width: 699px) {
      padding-top: 25px;
      border-radius: $borderRadius22;
    }

    @media (min-width: $xl) {
      display: grid;
      grid-template-columns: minmax(430px, 1fr) minmax(255px, 1fr);
    }

    & .card-club__header {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding-left: 25px;
      padding-right: 25px;
      margin-top: 10px;

      @media (min-width: 699px) {
        padding-left: 18px;
        padding-right: 18px;
      }

      @media (min-width: $xl) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
        padding-right: 0;
      }
    }

    & .card-club__raiting {
      display: none;
      @media (min-width: 699px) {
        display: flex;
      }
    }

    & .card-club__title-and-raiting {
      width: 100%;
      @media (min-width: 699px) {
        width: 95%;
      }
    }

    & .card-club__title {
      width: 100%;
      padding-right: 25px;
      padding-left: 25px;

      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 2;

      @media (min-width: $md) {
        padding-right: 70px;
        padding-left: 35px;
      }
    }

    & .card-club__title span {
      @media (min-width: 699px) {
        display: inline-block;
      }
    }

    & .card-club__footer {
      flex-direction: column-reverse;
      align-items: flex-start;
      gap: 10px;
      width: 100%;

      @media (max-width: 699px) {
        padding: 0 20px;
        margin-bottom: 25px;
      }

      @media (max-width: 699px) {
        & .link-icon {
          font-style: normal;
          font-weight: 400;
          font-size: 15px;
          line-height: 140%;
          margin-bottom: 0;
          padding: 10px 24px;
          border-radius: $borderRadius;

          &:first-child {
            padding-right: 24px;
            color: $primary-color;
            border: 1px solid $primary-color;
          }
          &:last-child {
            color: $white-color;
            background-color: $red-color;
          }
        }
      }

      @media (max-width: 365px) {
        & .link-icon {
          padding-right: 14px;
        }
      }

      @media (min-width: 699px) {
        flex-direction: row;
      }

      @media (min-width: $xl) {
        flex-direction: column;
        align-items: flex-end;

        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;

        gap: 20px;

        padding-left: 0;
        justify-content: flex-start;
        padding-top: 12px;
      }

      & .link-icon:first-child {
        margin-bottom: 0;

        @media (min-width: $xl) {
          margin-bottom: 0.5rem;
        }
      }
    }

    & .card-club__pic {
      order: 2;
      margin-bottom: 0;
      width: 100%;
      // height: 133px;  //  
      height: 100px; // b-  

      display: none;
      @media (min-width: 699px) {
        display: flex;
      }

      @media (min-width: $xl) {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 3;
        grid-row-end: 4;
      }

      & img {
        width: 100%;
        height: 100%;
      }
    }

    & .card-club__working.working-hours {
      display: flex;
      margin: 10px 0;

      @media (min-width: 699px) {
        display: none;
      }

      @media (min-width: $xl) {
        display: flex;
        order: 2;
        margin: 0;
        margin-left: 0;
        margin-right: auto;
      }
    }

    & .working-hours__club {
      flex-direction: column;

      @media (min-width: 699px) {
        flex-direction: row;
        text-transform: lowercase;
      }

      &::before {
        top: 1px;
      }
    }

    & .working-hours__club-label {
      padding-right: 13px;
    }
  }
}
