.header-club {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;

    @media (min-width: $xs) {
      margin-bottom: 35px;
    }
  }

  &__title {
    margin: 0;
    font-size: 27px;
    text-transform: uppercase;
    letter-spacing: 0.02em;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;
    /* or 35px */

    letter-spacing: 0.02em;
    text-transform: uppercase;

    margin-bottom: 15px;

    @include ff(
      'font-size',
      (
        375px: 27px,
        768px: 50px,
        1280px: 60px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 15px;
      font-size: 40px;
      letter-spacing: 0.02em;
    }
    @media (min-width: $md) {
      font-size: 50px;
      margin-bottom: 35px;
    }

    & span {
      display: block;

      @media (min-width: $md) {
        display: inline-flex;
      }
    }

    & div {
      line-height: 130%;
      @include ff(
        'font-size',
        (
          375px: 20px,
          768px: 30px,
        )
      );

      color: rgba(73, 76, 79, 0.5);
    }
  }

  &__item {
    width: 100%;
    margin-bottom: 1rem;

    &:first-child {
      flex-grow: 1;

      @media (min-width: $xs) {
        width: 30%;
        margin-bottom: 0;
      }
    }

    @media (min-width: $xs) {
      width: 60%;
      margin-bottom: 0;
    }

    & .raiting {
      margin-bottom: 0.5rem;
      @media (min-width: $md) {
        margin-bottom: 0;
      }
    }
  }

  &__map {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    font-weight: 400;
    font-size: 20px;
    line-height: 130%;

    & .link-text {
      margin-top: 0.5rem;
      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      text-decoration: none;
      color: #b3b3b3;
      text-transform: initial;

      &:focus,
      &:hover {
        &.link-text--more {
          color: $primary-color;
        }
        &.link-text--more .icon {
          width: 40px;
          transform: translateX(6px);
          transition: $transition-base;
        }
      }
    }
  }

  &__wh {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 30px;
    width: 100%;

    @media (min-width: 576px) {
      margin-bottom: 0;
    }
    @media (min-width: 697px) {
      flex-direction: row;
      width: 55%;
    }
    @media (min-width: $sm) {
      justify-content: flex-end;
      width: 50%;
    }
    @media (min-width: 930px) {
      width: 44%;
    }
    @media (min-width: 955px) {
      width: 70%;
    }
  }
  &__wh-item {
    position: relative;

    & .subtitle {
      margin-bottom: 5px;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
    }

    &--br {
      @media (min-width: $sm) {
        padding-right: 7px;
      }
      @media (min-width: 930px) {
        padding-right: 10px;
      }
      @media (min-width: $md) {
        padding-right: 30px;
      }
      &::before {
        @media (min-width: 697px) {
          position: absolute;
          content: '';
          z-index: 1;
          top: 0;
          right: 0;
          width: 1px;
          height: 100%;
          background-color: $primary-color;
        }
      }
    }
  }

  &__wh-address,
  &__wh-label {
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
  }
}
