.page-schedule {
  margin-bottom: 54px;

  @media (min-width: $sm) {
    padding-top: 30px;
  }
  @media (min-width: $xl) {
    margin-bottom: 84px;
  }

  & .title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: $primary-color;

    @media (min-width: $sm) {
      font-size: 50px;
    }
    @media (min-width: $xl) {
      font-size: 60px;
      color: rgba(73, 76, 79, 0.5);
    }
  }

  &__form {
    display: flex;
    flex-wrap: wrap;

    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 84px;
    }

    @media (min-width: $md) {
      margin-bottom: 84px;
      background: $gray-light-color;
      box-shadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;
    }
  }

  &__advantaves {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }

  & .record {
    margin-bottom: 0;
    @media (min-width: $xl) {
      margin-top: 50px;
    }
  }

  & .record__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    @media (min-width: $sm) {
      font-size: 30px;
    }
    @media (min-width: $xl) {
      font-size: 60px;
    }
  }
}
