.form-schedule {
  background-color: $gray-light-color;

  padding: 24px 15px;

  overflow: hidden;

  @media (max-width: $sm) {
    box-shadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;
    border-radius: 10px;
  }

  @media (min-width: 345px) {
    padding-bottom: 34px;
  }
  @media (min-width: $xs) {
    padding: 25px 15px 50px;
    border-radius: 22px;
  }
  @media (min-width: $md) {
    padding: 40px 15px;
  }

  &.container {
    @media (max-width: $md) {
      margin: 0 15px;
    }
  }

  &__title {
    margin-bottom: 0.7rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: #ffffff;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 40px,
      )
    );

    @media (min-width: $xs) {
      margin-bottom: 1rem;
    }
    @media (min-width: $sm) {
      margin-bottom: 2rem;
    }
  }

  & .form-clubs__title {
    font-size: 24px;

    @media (min-width: $sm) {
      font-size: 40px;
      line-height: 130%;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    width: 100%;
  }

  & .form-clubs__wrapper {
    gap: 18px;
    font-size: 20px;
    line-height: 130%;

    @media (min-width: $md) {
      gap: 0;
    }
  }

  & .form-control {
    font-size: 20px;
    line-height: 130%;
    padding: 0.7rem 1.75rem 0.7rem 1.45rem;

    @media (min-width: $xs) {
      font-size: 15px;
    }
    @media (min-width: $sm) {
      padding: 1rem 1.8rem 1rem 1rem;
      font-size: 20px;
    }
  }

  & .form-label {
    font-size: 15px;
    line-height: 140%;
  }

  & .form-label,
  & .form-clubs__btns {
    padding-bottom: 0;
    // margin-bottom: 0.5rem;
    color: #ffffff;
    width: 100%;

    @media (min-width: $xs) {
      width: calc(100% / 2 - 1rem);
      // margin-right: 0.5rem;
    }

    @media (min-width: $md) {
      width: calc(23% - 1rem);
      margin-right: 1rem;
    }
    @media (min-width: $xl) {
      width: calc(23% - 1rem);
      margin-right: 1rem;
    }
    @media (min-width: $xxl) {
      width: calc(24% - 30px);
      margin-right: 30px;
    }
  }

  & .form-clubs__btns {
    margin-top: 0;

    @media (min-width: $md) {
      width: calc(31% - 1rem);
    }
    @media (min-width: $xxl) {
      width: calc(100% / 4);
      margin-right: 0;
    }
  }

  & .form-select {
    &:focus,
    &:hover {
      box-shadow: 0 0 0 0.15rem rgba(44, 58, 69, 0.305);
    }
    @media (min-width: $xl) {
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 1rem;

    @media (min-width: 375px) {
      flex-direction: row;
    }
    @media (min-width: $xs) {
      width: auto;
      margin-top: 0;
    }
    @media (min-width: $md) {
      margin-left: auto;
      margin-right: 0;
    }
  }

  & .button {
    flex-grow: 1;
    width: 100%;
    margin-bottom: 1rem;
    padding: 14px 20px;
    font-size: 20px;
    line-height: 130%;

    @media (min-width: $xs) {
      font-size: 15px;
    }
    @media (min-width: $sm) {
      font-size: 20px;
    }

    &:not(:last-child) {
      margin-right: 0.5rem;

      @media (min-width: $xxl) {
        margin-right: 24px;
      }
    }

    @media (min-width: 375px) {
      width: calc(100% / 2 - 0.25rem);
      margin-bottom: 0;
    }
    @media (min-width: $xs) {
      // margin-top: 1rem;
      // margin-bottom: 0.4rem;
      padding: 10px 20px;
    }
    @media (min-width: $sm) {
      padding: 16px 20px;
    }
    @media (min-width: $md) {
      // width: auto;
      padding: 16px;
    }
    @media (min-width: $xxl) {
      width: calc(100% / 2 - 24px);
    }
  }
}
