:root {
  --schedule-col: calc(100% / 7 - 10px);
  --schedule-col-min: 166px;
  --schedule-col-max: 100%;
  --schedule-min: 980px;
  --schedule-row: 40px;

  --schedule-row-min: 40px;
  --schedule-row-empty: 40px;
  --schedule-row-empty-min: 40px;
  --base-gap: 5px;
  --step: 5px;
  --count: 2;
  --bg-image: url('../img/schedule/bg-mb.png');
  --item-color: rgb(234 236 237 / 45%);
  --week-days-h: 50px;

  @media (min-width: $xs) {
    --schedule-row: 100px;
    --schedule-row-min: 100px;
    --schedule-col-max: 166px;
    --schedule-row-empty: 166px;
    --schedule-row-empty-min: 166px;
  }
  @media (min-width: $sm) {
    --schedule-row: 100px;
    --schedule-row-min: 100px;
    --schedule-col-max: 166px;
    --schedule-col-min: 150px;

    --bg-image: url('../img/schedule/bg.png');
  }
  @media (min-width: $md) {
    --schedule-col-min: 110px;
    --schedule-col-max: calc(86vw /7);
    --schedule-min: 700px;
  }
  @media (min-width: $xl) {
    --base-gap: 8px;
    --schedule-min: 980px;
    --schedule-row: 166px;

    --schedule-col: calc(100% / 7);
    --schedule-col-max: calc(100% / 7);
  }

  @media (min-width: $xxl) {
    --schedule-col-min: 166px;
    --schedule-col: calc(100% / 7 - 10px);
    --schedule-col-max: calc(100% / 7 - 10px);
  }
}

.schedule {
  & .schedule__warning {
    display: none;
  }

  & .schedule__header-link-pdf,
  & .schedule__header-link-next {
    display: flex;
  }

  & .schedule__header-link-next {
    margin-bottom: 20px;

    & span {
      &::first-letter {
        text-transform: uppercase;
      }
    }
  }

  &__header {
    position: relative;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    @media (max-width: $xs) {
      display: inline-flex;
    }

    @media (min-width: $xs) {
      flex-direction: row;
      justify-content: space-between;
    }
    @media (min-width: $sm) {
      flex-direction: column;

    }

    &-title.title {
      margin-bottom: 30px;

      @media (min-width: $sm) {
        margin-bottom: 15px;
        font-size: 30px;
      }
      @media (min-width: $xl) {
        font-size: 60px;
      }
    }

    &-link-pdf,
    &-title {
      width: 100%;

      @media (min-width: $xs) {
        width: auto;
      }
    }

    &-link-pdf {
      @include linkReset();
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 20px;
      padding: 10px 25px 12px;
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 17px;
      line-height: 120%;
      text-align: center;
      color: $primary-color;
      border: 2px solid #42474d;
      border-radius: 16px;

      @media (min-width: $sm) {
        display: inline-flex;
        justify-content: flex-start;
        position: absolute;
        top: 5px;
        right: 0;
        text-align: left;
      }

      @media (min-width: $xl) {
        top: 10px;
      }

      & span {
        display: inline-flex;
      }

      &:focus,
      &:hover {
        opacity: 0.8;
        color: $primary-color;
      }
    }

    & .link-text {
      &:focus,
      &:hover {
        & svg {
          width: 40px;
          transform: translateX(6px);
          transition: $transition-base;
        }
      }
    }

    &-scroll {
      display: none;

      @media (min-width: $xs) {
        display: flex;
        margin-right: 0;
        margin-left: auto;
        margin-top: 10px;
        margin-bottom: 10px;
      }
      @media (min-width: 1024px) {
        display: none;
      }
    }
  }

  &__inner {
    position: relative;
    display: flex;
    flex-wrap: wrap;

    margin-bottom: 30px;
    box-sizing: border-box;

    @media (max-width: $xs) {
      justify-content: center;
      // flex-wrap: nowrap;

      max-width: 100%;
      margin: 0 auto;
      padding-top: 90px;
    }
  }

  &__hours {
    width: 56px;
    margin-top: 56px;

    @media (max-width: $sm) {
      margin-top: 0;
    }
  }

  &__week {
    // position: relative;
    display: flex;
    flex-direction: column;
    width: calc(100% - 70px);
    overflow: hidden;
    // &::-webkit-scrollbar-track {
    //   margin: 10px 0px;
    // }

    scroll-behavior: smooth;

    @media (max-width: $sm) {
      width: calc(100% - 65px);
    }

    @media (max-width: 1400px) {
      overflow-x: auto;
    }
  }

  &__week-days {
    position: relative;
    display: flex;
    gap: var(--base-gap);

    // margin-left: 4px;
    @media (min-width: $xs) {
      // margin-bottom: 10px;
      height: var(--week-days-h);
      margin-bottom: 10px;
    }
  }

  &__days {
    display: flex;

    width: 100%;
    gap: var(--base-gap);

    border-radius: 12px;

    @media (min-width: $sm) {
      min-width: var(--schedule-min);
    }

    &--close {
      display: none;
    }
  }

  &__button {
    @include buttonReset();
    display: none;
    position: relative;
    width: 100%;
    padding: 17px;
    background: #e2f0fb;
    /* 3d */

    box-shadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;
    border-radius: 16px;

    @media (max-width: $sm) {
      display: flex;
      justify-content: center;
    }

    &--open {
      &::before {
        transform: translate(-50%, -50%) rotate(180deg);
      }
    }
  }

  &__mob-nav {
    display: none;

    @media (max-width: $xs) {
      display: flex;
      margin-top: 8px;
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
    }

    & .schedule__button {
      display: flex;
      align-items: center;

      & .icon {
        margin: 0 5px;
        width: 15px;
        height: 15px;
      }
    }
  }

  &__item-active {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    color: $primary-color;

    &--active {
      color: $primary-color;
      @media (min-width: $xs) {
        background-color: $contrast-color;
        border-color: $contrast-color;
      }
    }
  }
}

.week-days {
  @media (max-width: $xs) {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    // width: 230px;
  }

  &--open {
    position: absolute;
    z-index: 1;
    top: 12px;
    left: -2px;
    padding-top: 60px;

    padding-bottom: 20px;
    width: 100%;
    max-width: 100%;
    // max-height: 360px;
    // overflow: auto;

    background-color: #fff;
    box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.25);
    border-radius: 16px;

    & .week-days__item {
      display: flex;

      &--hidden {
        display: none;
      }
    }
  }

  &__item {
    order: 1;
    display: flex;
    // justify-content: center;
    align-items: center;
    // margin: 4px;
    padding: 10px 40px 10px 18vw;

    width: 100%;
    min-width: var(--schedule-col-min);
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;

    @media (min-width: $xs) {
      padding: 10px;
      justify-content: center;
      text-transform: lowercase;
    }

    &--active {
      color: $white-color;
      background-color: $primary-color;
      border-color: $primary-color;
      border-radius: 12px;
    }

    &--first {
      order: 0;
    }

    & span {
      display: inline-flex;
    }

    @media (max-width: $xl) {
      width: var(--schedule-col);
      flex-grow: 1;
      font-size: 18px;
    }

    @media (max-width: $xs) {
      display: none;
      // flex-direction: column;
      margin: 0;
      // padding: 16px 0;

      width: 100%;
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 17px;
      line-height: 120%;
    }
  }
}

.days {
  display: flex;
  flex-wrap: wrap;
  width: var(--schedule-col);
  min-width: var(--schedule-col-min);
  max-width: var(--schedule-col-max);
  flex-grow: 1;
  gap: var(--base-gap);
  // margin: 4px 0;

  @media (max-width: $xs) {
    display: none;
  }

  &--hidden {
    display: none;
  }

  &--active {
    display: flex;
  }

  &__item {
    flex-grow: 1;
    display: flex;

    justify-content: center;
    align-items: center;

    // margin: 4px 0;
    // margin-bottom: 8px;
    // width: 148px;
    width: 100%;
    min-width: var(--schedule-col-min);
    // height: var(--schedule-row);
    min-height: var(--schedule-row-min);
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;

    color: $primary-color;
    // background: #eaeced;
    background: var(--item-color);
    border-radius: 12px;
    border: 2px solid transparent;

    // resize: both;
    overflow: hidden;
    transition: max-height 0.9s;

    &--active {
      color: $primary-color;
      border: 2px solid $primary-color;
    }
  }
}

.hours {
  display: flex;

  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  gap: var(--base-gap);

  // margin-top: 4px;

  height: 100%;
  flex-grow: 1;

  @media (max-width: $xs) {

  }

  @media (min-width: $xs) {
    gap: var(--base-gap);
    margin-top: calc(var(--week-days-h) + 10px);
  }

  @media (min-width: $md) {
    // margin-top: 60px;
    // margin-top: 53.4px;
  }



  &--opacity {
    opacity: 0.5;
  }

  &__item {
    display: flex;

    justify-content: center;
    align-items: center;
    padding: 5px;
    width: 100%;
    height: var(--schedule-row);
    min-height: var(--schedule-row-min);

    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    // border: 2px solid transparent;


    &--active {
      color: #ffffff;
      background-color: $primary-color;
      border-color: $primary-color;
      border-radius: 12px;
    }
  }

  &__evening,
  &__day,
  &__morning {
    gap: var(--base-gap);
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    // margin-bottom: 10px;

  }

  &__evening {
    // margin-bottom: 0;
    background: #d0d0d0;
  }
  &__day {
    // margin-bottom: 14px;
    background: #dcddde;
  }
  &__morning {
    background: #eaeced;
  }
}

@keyframes mobDaysVisible {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.schedule--empty {
  & .schedule__days {
    background-color: #4b8aa6;
    background-image: var(--bg-image);

    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.7;
  }

  & .days__item {
    min-height: var(--schedule-row-empty-min);
    height: var(--schedule-row-empty);
    @media (max-width: $xs) {
      --schedule-row: 0;
    }
  }

  & .hours__item {
    min-height: var(--schedule-row-empty-min);
    height: var(--schedule-row-empty);
    @media (max-width: $xs) {
      --schedule-row: 0;
    }
  }

  & .schedule__header-link-pdf,
  & .schedule__header-link-next {
    display: none;
  }

  & .schedule__warning {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding: 10px 15px 10px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    color: $primary-color;
    border: 2px solid #42474d;
    border-radius: 16px;

    @media (min-width: $sm) {
      position: absolute;
      top: 0;
      right: 0;
    }

    @media (min-width: $xl) {
      top: 10px;
    }

    & .icon {
      width: 16px;
      height: 16px;
      margin-right: 5px;
    }

    & span {
      width: calc(100% - 20px);
    }
  }
}
