.subscription {
  display: flex;
  flex-direction: column;
  margin: 1rem 0 3rem;
  padding: 30px 15px;
  width: 100%;
  background-color: $gray-light-color;
  border-radius: $borderRadius22;

  @media (min-width: $xs) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 35px 55px;
  }

  @media (min-width: $xl) {
    padding: 35px 80px;
    margin: 20px auto 3rem;
    max-width: 1150px;
  }

  &__title {
    width: 100%;
    margin-bottom: 1.6rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    color: $white-color;
    max-width: 850px;

    @media (min-width: $xs) {
      font-size: 19px;
    }
    @media (min-width: $md) {
      font-size: 27px;
      line-height: 130%;
    }

    & span {
      display: block;
      word-spacing: inherit;
      @media (min-width: $sm) {
        display: inline-block;
      }
    }
  }

  &__form {
    width: 100%;
    max-width: 850px;

    // @media (min-width: 601px) {
    //   width: calc(100% - 365px);
    // }

    // @media (max-width: 600px) {
    //   width: 100%;
    // }
  }
}
