.list-card {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 35px;
  margin-bottom: 35px;
  transition: $transition-base;

  @media (min-width: $xs) {
    display: flex;
    // flex-wrap: nowrap;
    justify-content: space-between;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 10px;
    padding-right: 10px;
  }

  @media (min-width: $md) {
    gap: 40px;
    margin-top: 54px;
    // margin-bottom: 44px;
  }

  &--three {
    // grid-auto-columns: minmax(288px, 302px);

    width: 100%;
    padding-right: 10px;
    @media (min-width: $xs) {
      grid-template-columns: repeat(auto-fill, minmax(288px, 1fr));
    }

    @media (min-width: $md) {
      grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
      grid-auto-columns: minmax(288px, 400px);
      // grid-auto-flow: column;
      gap: 20px;
    }
    @media (min-width: $xxl) {
      grid-template-columns: repeat(auto-fill, minmax(288px, 395px));
      grid-auto-columns: minmax(288px, 400px);
      // grid-auto-flow: column;
      gap: 20px;
    }

    @media (min-width: 1410px) {
      grid-template-columns: repeat(auto-fill, minmax(288px, 400px));
    }

    & .list-card__item {
      min-width: 288px;
      @media (min-width: $xs) {
        // width: 300px;
        // max-width: 300px;
      }
      @media (min-width: $md) {
        width: 400px;
        max-width: 400px;
      }
      @media (min-width: $xl) {
        width: 400px;
      }
    }
  }

  &--four {
    // display: flex;
    flex-direction: column;

    @media (min-width: $xs) {
      flex-direction: row;
      gap: 22px;
      // grid-template-columns: repeat(auto-fill, minmax(288px, 300px));
      // grid-auto-columns: minmax(288px, 300px);
      // grid-auto-flow: column;
    }

    @media (min-width: $xs) {
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
    @media (min-width: $md) {
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
    @media (min-width: 1440px) {
      grid-template-columns: repeat(auto-fill, 300px);
    }

    & .list-card__item {
      @media (min-width: $xs) {
        // width: 300px;
        // max-width: 300px;
        // width: 100%;
      }
    }
  }

  &__item {
    width: 100%;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $xs) {
      margin-bottom: 0;
    }
  }
}

#scroll-cards {
  @media (min-width: $xs) {
    overflow: hidden;
    width: 100%;
  }

  & .list-card {
    @media (min-width: $xs) {
      padding-top: 5px;
      // flex-wrap: nowrap;
    }
    @media (min-width: $md) {
      margin-top: 0;
      margin-bottom: 0;
    }
  }
}

