/**
    * Reinstate scrolling for non-JS clients
    */
.simplebar-content-wrapper {
  scrollbar-width: auto;
  -ms-overflow-style: auto;
}

.simplebar-content-wrapper::-webkit-scrollbar,
.simplebar-hide-scrollbar::-webkit-scrollbar {
  display: initial;
  width: initial;
  height: initial;
}

.mCustomScrollbar {
  & .list-card--three {
    grid-auto-columns: minmax(288px, 302px);
    grid-auto-flow: column; //     

    @media (min-width: $md) {
      grid-auto-columns: minmax(288px, 400px);
      // grid-auto-flow: column;
      gap: 20px;
    }

    @media (min-width: $xl) {
      grid-template-columns: repeat(3, 1fr);
    }

    & .list-card__item {
      min-width: 288px;
      @media (min-width: $xs) {
        width: 300px;
        max-width: 300px;
      }
    }
  }

  & .list-card--four {
    grid-auto-flow: column; //     

    & .list-card__item {
      @media (min-width: $xs) {
        width: 300px;
        max-width: 300px;
      }
    }
  }
}
