.form-subscription {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;

  & .form-label {
    width: 100%;
    padding-bottom: 1rem;
    flex-grow: 1;
    color: $white-color;
    @media (min-width: 600px) {
      padding-bottom: 1.5rem;
      width: calc(50% - 1rem);
      flex-grow: 0;
    }

    @media (min-width: $md) {
      flex-grow: 1;
      width: calc(30% - 80px);
      margin-right: 2rem;
    }
  }

  & .form-control {
    width: 100%;
    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;

    @media (min-width: $md) {
      padding-top: 1.1rem;
      padding-bottom: 1.1rem;
    }

    &::placeholder {
      font-size: 20px;
    }
  }

  & .form-select {
    & option {
      font-size: 20px;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-right: 0;
    margin-left: auto;
    padding-top: 0.5rem;

    @media (min-width: 600px) {
      width: 50%;
      align-content: start;
      justify-content: start;
      padding-top: 0;
      padding-left: 1rem;
      padding-right: 50px;
    }

    @media (min-width: $md) {
      width: auto;
      padding-top: 2.5rem;
      margin-right: 0;
      padding-left: 0;
      padding-right: 0;
    }
    @media (min-width: $xl) {
      padding-top: 2.2rem;
    }
  }

  & .button {
    width: 100%;
    box-shadow: $boxShadowdark;
    border-width: 1px;

    @media (min-width: $xs) {
      width: auto;
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  & .custom-form-check label {
    color: $white-color;
  }
}
