.modal-preview-club {
  & .modal-dialog {
    margin: 0 auto;
  }
  & .modal-content {
    border-radius: $borderRadius22;
    background-color: #f6f6f6;
  }
  & .modal-body {
    padding: 0;
  }

  & .club-preview__header {
    padding: 10px 30px;
  }

  & .club-preview__address {
    font-size: 16px;
  }

  & .working-hours__club {
    font-size: 16px;
  }

  & .club-preview__footer .link-text {
    @media (max-width: $xs) {
      padding-left: 24px;
      padding-right: 24px;
    }
  }

  & .club-preview__pic {
    height: 183px;

    & img {
      width: 100%;
      height: 100%;
    }
  }

  & .club-preview {
    padding-bottom: 22px;
  }

  & .club-preview .title {
    @media (max-width: $xs) {
      padding-right: 50px;
    }
    & span {
      display: inline-block;
      white-space: nowrap;
    }
  }
}
