:root {
  --base-mb: 30px;
  --form-px: 15px;
  --form-gap: 20px;
  --slider-h: 328px;
  @media (min-width: $xs) {
    --base-mb: 35px;
    --form-px: 55px;
  }
  @media (min-width: $md) {
    --base-mb: 54px;
    --form-gap: 60px;
    --slider-h: 500px;
  }
  @media (min-width: $xl) {
    --form-px: 137px;
  }
}

.page-presale {
  &.container {
    @media (min-width: $xl) {
      margin-bottom: 50px;
    }
  }

  &__slider {
    width: 100%;
    overflow: hidden;
    margin-bottom: var(--base-mb);
    max-height: 500px;

    & .swiper-slide {
      max-height: 100%;
    }
  }

  &__timer {
    width: 100%;
    overflow: hidden;
    margin-bottom: var(--base-mb);
    @media (min-width: $md) {
      padding: 0 40px;
    }

    & .timer__wrapper {
      max-width: 100%;
      @media (min-width: $md) {
        width: 66%;
      }
    }
    & .title {
      @media (min-width: $md) {
        width: 34%;
      }
    }

    & .timer__item {
      width: calc(100% / 4);
      height: 80px;

      @media (min-width: 375px) {
        height: 130px;
      }
      @media (min-width: $xs) {
        height: 145px;
      }
      @media (min-width: $lg) {
        height: 180px;
      }
    }
  }

  &__form {
    padding: 30px var(--form-px);
    margin-bottom: var(--base-mb);
    background-color: $blue-color;

    &.form .invalid-feedback,
    &.form .valid-feedback {
      bottom: -10px;
    }

    & .form__wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      align-items: flex-start;

      column-gap: var(--form-gap);
      row-gap: 10px;

      @media (min-width: $md) {
        justify-content: space-between;
      }
    }

    & .form-label {
      flex-grow: 1;
      padding-bottom: 0.5rem;
    }

    & .form__title {
      margin-bottom: 20px;
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 20px;
      line-height: 130%;

      color: $primary-color;

      @media (min-width: $xs) {
        font-size: 19px;
        line-height: 120%;
      }
      @media (min-width: $md) {
        margin-bottom: 30px;
        font-size: 17px;
      }
    }

    & .botton {
      margin-bottom: 15px;
      padding: 14px 44px;
      width: 100%;
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 20px;
      line-height: 130%;

      color: $primary-color;
      box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.25);
      border-radius: 16px;

      @media (min-width: $xs) {
        width: auto;
      }

      @media (min-width: 836px) {
        margin-top: 1.5rem;
        margin-bottom: 0;
      }
    }
  }
}
