.schedule-promo {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;

  font-weight: 400;
  font-size: 16px;
  line-height: 130%;

  color: $primary-color;

  @media (min-width: $sm) {
    margin-bottom: 54px;
    font-size: 20px;
  }
  &__info {
    width: 100%;
    @media (min-width: $md) {
      width: 50%;
    }
  }

  &__pic {
    width: 100%;
    min-height: 322px;
    background: #d9d9d9;
    background-image: url('../img/schedule/header-mb.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    border-radius: 15.535px;
    overflow: hidden;
    @media (min-width: $xs) {
      max-height: auto;
      min-height: 400px;
      background-image: url('../img/schedule/header-tb.jpg');
    }

    @media (min-width: $md) {
      min-height: 182px;
      margin-left: 40px;
      width: calc(50% - 40px);
      background-image: url('../img/schedule/header.jpg');
    }
  }

  & .title {
    margin-bottom: 15px;

    @media (min-width: $sm) {
      margin-bottom: 6px;
    }
  }

  & .text {
    margin-bottom: 30px;
    @media (min-width: $sm) {
      padding-right: 35px;
    }
  }
}
