.content {
  color: $primary-color;
  font-family: $mainFont;
  font-weight: 400;
  font-size: 15px;
  line-height: 140%;
  margin-bottom: 30px;

  @media (min-width: $xxl) {
    margin-bottom: 3.3rem;
  }

  &__slider {
    margin-bottom: 50px;
    @media (min-width: $xxl) {
      margin-bottom: 3.3rem;
    }
  }

  & .slider .swiper-slide {
    // max-height: 300px;
    @media (min-width: $md) {
      min-height: 500px;
    }
  }

  // & .slider .swiper-slide img {
  //   height: 100%;
  //   min-height: 295px;
  //   @media (min-width: $md) {
  //     min-height: 100%;
  //     min-height: 500px;
  //   }
  // }

  & h1 {
    margin-bottom: 10px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;
    /* identical to box height, or 78px */

    letter-spacing: 0.02em;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: rgba(73, 76, 79, 0.5);

    @include ff(
      'font-size',
      (
        375px: 32px,
        1280px: 60px,
      )
    );

    @media (min-width: $sm) {
      margin-bottom: 0.8rem;
    }
  }

  & p {
    margin-bottom: 0;
    color: inherit;

    font-size: 13px;

    @media (min-width: $xs) {
      font-size: 15px;
    }
  }

  & a {
    color: $primary-color;
    text-decoration: underline;

    &:hover {
      color: $primary-color;
    }
  }

  &--promo {
    & p {
      font-size: 20px;
      line-height: 130%;
    }
  }
}
