.modal-filter-list-clubs {
  & .modal-dialog {
    margin: 1rem auto;
  }
  & .modal-content {
    padding: 0;
    margin: 0 auto;
    max-width: 320px;
    background-color: transparent;
    border: none;
    border-radius: $borderRadius22;

  }
  & .modal-body {
    padding: 0;
    border-radius: $borderRadius22;
    background-color: #f6f6f6;
    overflow: hidden;
  }
}
