.sale {
  display: block;
  position: relative;
  background-position: 50%;
  background-size: cover;
  background-color: $blue-color;

  min-height: 320px;

  overflow: hidden;

  @media (min-width: $sm) {
    min-height: 550px;
  }
  @media (min-width: $xl) {
    min-height: 630px;
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: fill;
  }
}
