.mob-week-days {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  gap: 8px;
  padding: 30px 0 20px;

  @media (min-width: $sm) {
    display: none;
  }

  &__item {
    @include buttonReset();
    padding: 8px;
    flex-grow: 1;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    text-align: center;
    color: $primary-color;

    background: #eaeced;
    border-radius: 6px;

    &--active {
      background-color: $blue-color;
    }
  }
}
