.advantages-workout {
  margin-bottom: 30px;
  @media (min-width: $sm) {
    margin-bottom: 50px;
  }
  &__title {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 30px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: $primary-color;
    @media (min-width: $sm) {
      font-size: 30px;
    }
    @media (min-width: $xl) {
      margin-bottom: 35px;
      font-size: 40px;
    }

    & span {
      padding: 0 5px;
      font-family: $mainFont;
      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      text-transform: lowercase;
    }
  }

  &__list {
    @include listReset();
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (min-width: $xs) {
      gap: 18px;
    }
    @media (min-width: $sm) {
      gap: 10px;
    }
    @media (min-width: $md) {
      gap: 45px;
    }
    @media (min-width: $xl) {
      padding-right: 10%;
    }
  }

  &__item {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    align-items: center;
    padding: 20px 16px;
    margin-bottom: 13px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;

    background: #e2f0fb;

    box-shadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;
    border-radius: 12px;

    @media (min-width: $xs) {
      padding: 20px 13px;
      width: calc(100% / 3 - 12px);
      flex-direction: column;
      text-align: center;
    }

    @media (min-width: 700px) {
      flex-direction: row;
      text-align: left;
      margin-bottom: 0;
      width: calc(100% / 3 - 12px);
    }
    @media (min-width: 800px) {
      padding: 20px 16px;
    }
    @media (min-width: $md) {
      width: calc(100% / 3 - 45px / 1.5);
      margin-bottom: 13px;
    }
    @media (min-width: $md) {
      padding: 35px;
      font-size: 20px;
      line-height: 130%;
    }

    & span {
      width: calc(100% - 40px);

      @media (min-width: $xs) {
        width: 100%;
      }

      @media (min-width: 700px) {
        width: calc(100% - 40px);
      }
    }

    & .icon {
      width: 30px;
      // margin-top: 5px;
      margin-right: 10px;
    }

    &--center {
      @media (min-width: $sm) {
        padding: 20px;
        justify-content: center;
        text-align: center;

        & .icon {
          // margin-left: auto;
          width: 20px;
          margin-right: 10px;
        }

        & span {
          width: auto;
        }
      }

      @media (min-width: $md) {
        & span {
          width: auto;
        }

        & .icon {
          margin-left: 0;
          margin-right: 10px;
        }
      }
    }
  }
}
