.full-lesson {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;

  font-weight: 400;
  font-size: 16px;
  line-height: 130%;

  & > * {
    flex-grow: 1;
    padding: 20px;
    width: calc(100% / 6);

    &:first-child {
      width: 160px;
      min-width: 160px;
    }

    &:nth-child(2) {
      min-width: 208px;
    }
  }
}
