.presale {
  margin: 0 auto;
  max-width: 1275px;
  margin-bottom: 35px;
  margin-top: 35px;

  @media (min-width: $md) {
    margin-bottom: 54px;
    margin-top: 54px;
  }

  &__list {
    position: relative;
    @include listReset();

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $primary-color;
    }
  }

  &__item {
    flex-grow: 1;
    position: relative;
    padding: 21px 0;
    margin: 0;
    color: $primary-color;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    overflow: hidden;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -0.5%;
      width: 100%;
      height: 1%;
      background-color: $primary-color;

      @media (min-width: $md) {
        height: 1.25%;
      }
    }

    @media (min-width: $xs) {
      padding: 25px 0;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @media (min-width: $md) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 1fr;
      align-items: center;
      padding-bottom: 15px;
      padding-right: 10px;
    }
  }

  &__item-club {
    padding-left: 25px;
    padding-right: 75px;
    margin-bottom: 17px;
    flex-grow: 1;

    @media (min-width: 650px) {
      width: 60%;
      padding-right: 20px;
    }

    @media (min-width: $md) {
      // grid-row: 1/4;
      grid-row-start: 1;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 2;
      // grid-column: 1/2;

      width: 100%;
      margin-bottom: 0;
      margin-left: 25px;
    }
  }

  &__club-name {
    position: relative;
    font-family: $mainFontBold;
    font-weight: 700;
    line-height: 140%;
    font-size: 15px;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    @media (min-width: $xs) {
      line-height: 130%;
    }

    @include ff(
      'font-size',
      (
        375px: 15px,
        768px: 20px,
      )
    );

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -25px;
      width: 17px;
      height: 30px;
      background-image: url('../img/icons/fh.svg');

      @media (min-width: $xs) {
        top: 0;
        left: -25px;
      }
    }
  }

  &__club-address {
    font-family: $mainFont;

    font-weight: 400;
    font-size: 16px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        375px: 16px,
        768px: 20px,
      )
    );

    @media (min-width: $xs) {
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 20px;
    }
  }

  &__opening-date {
    display: flex;
    flex-direction: column;
    align-items: start;

    @media (min-width: $xs) {
      width: 100%;
    }
    @media (min-width: $md) {
      width: 100%;
      // grid-row: 1/2;
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 2;
      grid-column-end: 3;
      // grid-column: 2/3;
    }
  }
  &__date-inner {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row-reverse;
    align-items: flex-start;
    padding: 10px 40px 10px 25px;
    margin-bottom: 17px;
    margin-right: auto;
    background: $blue-color;
    border-radius: 100px;

    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;

    @media (min-width: $xs) {
      padding: 6px 15px;
      align-items: center;
      font-size: 20px;
    }

    @media (min-width: $md) {
      margin-left: 20px;
      margin-bottom: 7px;
    }

    & span {
      display: block;

      @media (min-width: $xs) {
        display: inline-block;
      }
    }

    & .icon {
      width: 25px;
      height: 23px;
      margin-right: 8px;
    }
  }

  &__links {
    padding-left: 25px;

    @media (min-width: $xs) {
      padding-left: 20px;
      flex-grow: 1;
    }

    @media (min-width: $md) {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 2;
      grid-column-end: 3;
      // grid-column: 2/3;
      padding-left: 25px;
    }

    & .link-icon {
      margin-bottom: 17px;

      &:last-child {
        margin-bottom: 0;
      }

      @media (min-width: $md) {
        margin-bottom: 0;
      }
    }
  }
}
